.app-nav-div{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
  padding-bottom: 10px;
  border-bottom: 1px solid white;
}

.app-nav-logo{
  width: 10%;
  height: 10%;
}
.app-nav-span{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

}
.app-nav-text{
  margin-right: 20px;
  color: white;
  font-size: 2vw;
}

.app-nav-sms-div{

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 10%;
}
.app-nav-sms-img{
  width: 20%;
  height: 20%;
  margin-right: 10px;

}

.app-nav-text:hover{
  cursor: pointer;
  filter: brightness(50%);
}
.app-nav-sms-img:hover{
  cursor: pointer;
  filter: brightness(50%);
}